import Button from "./components/Button";
import { useState } from "react";

const App = () => {
  const [result, setResult] = useState("0");
  const [history, setHistory] = useState("");

  const populateResult = (value) => {
    result !== "0" ? setResult(result + value) : setResult(value);
  };

  const populateHistory = (value) => {
    setHistory(`${result} ${value}`);
    setResult("0");
  };

  const runOperation = () => {
    if (history !== "") {
      setHistory(`${history} ${result}`);
      const opArray = history.split(" ");
      const resultOne = parseInt(opArray[0]);
      const resultTwo = parseInt(result);

      switch (opArray[1]) {
        case "/":
          setResult(`${resultOne / resultTwo}`);
          break;
        case "*":
          setResult(`${resultOne * resultTwo}`);
          break;
        case "-":
          setResult(`${resultOne - resultTwo}`);
          break;
        case "+":
          setResult(`${resultOne + resultTwo}`);
          break;
        default:
          break;
      }
    }
  };

  const clearFromBack = () => {
    const length = result.length;
    if (length !== 1) {
      const num = result.charAt(length - 1);
      const newString = result.replace(num, "");
      setResult(newString);
    } else {
      setResult("0");
    }
  };

  const clearAll = () => {
    setResult("0");
    setHistory("");
  };

  return (
    <div className="App">
      <div className="history-field">
        <p id="history">{history}</p>
      </div>
      <div className="result-field">
        <p id="result">{result}</p>
      </div>
      <div className="btn-container">
        <div className="btn">
          <Button value={"C"} click={clearAll} />
          <Button value={"7"} click={() => populateResult("7")} />
          <Button value={"4"} click={() => populateResult("4")} />
          <Button value={"1"} click={() => populateResult("1")} />
        </div>
        <div className="btn">
          <Button value={"CE"} click={clearFromBack} />
          <Button value={"8"} click={() => populateResult("8")} />
          <Button value={"5"} click={() => populateResult("5")} />
          <Button value={"2"} click={() => populateResult("2")} />
          <Button value={"0"} click={() => populateResult("0")} />
        </div>
        <div className="btn">
          <Button value={"%"} />
          <Button value={"9"} click={() => populateResult("9")} />
          <Button value={"6"} click={() => populateResult("6")} />
          <Button value={"3"} click={() => populateResult("3")} />
        </div>
        <div className="btn">
          <Button
            value={"/"}
            className={"red"}
            click={() => populateHistory("/")}
          />
          <Button
            value={"*"}
            className={"yellow"}
            click={() => populateHistory("*")}
          />
          <Button
            value={"-"}
            className={"blue"}
            click={() => populateHistory("-")}
          />
          <Button
            value={"+"}
            className={"pink"}
            click={() => populateHistory("+")}
          />
          <Button value={"="} className={"green"} click={runOperation} />
        </div>
      </div>
    </div>
  );
};

export default App;
